</template>

<script>
import axios from "axios"
import BaseFilter from "./BaseFilter.vue";
import ElementUI from "element-ui";
export default {
    name: "DialogFilter",
    components: {
        BaseFilter,
        ElementUI
    },
    props: {
        handleSubmit: {
            type: Function,
            required: false
        },
    //     title: String,
    //     content: String,
    //     confirmText: {
    //         type: String,
    //         default: "Ok"
    //     },

    },
    data() {
        return {
            maxPrice: 300000,
            minPrice: 0,
            offset: 0,
            limit: 50,
            certifiedHotel: true,
            key: '',
            category: '',
            value: 0,

            props: {
                multiple: true
            },
            value: [0, 3000000],
            state1: '',
            gia: 0,
            form: {
                name: '',
                region: '',
                date1: '',
                value1: 0,
                date2: '',
                delivery: false,
                type: [],
                resource: '',
                desc: '',
                danhmuc: '',
                gia: 0,
                value: '',
                checkList: [],
                checkboxGroup1: [],

            },

        };
    },
    created() {

    },
    methods: {
        formatTooltip(val) {
            return val / 1;
        },
        submitForm() {
            let resource = this.form.resource
            let gia = this.form.gia
            let danhmuc = this.form.danhmuc
            let checkList = this.form.checkboxGroup1
            this.handleSubmit && this.handleSubmit({resource, gia, danhmuc, checkList})
            // console.log('resource', resource)
            // console.log('gia', gia)
            // console.log('danhmuc', danhmuc)
            // console.log('checkList', checkList)

        },

        handleSelect(item) {
            console.log(item);
        },
        onSubmit() {
            console.log('Kt thc!');
        },
        changeLocale(locale) {
            i18n.locale = locale;
        },
        formatTooltip(val) {
            return val / 10;
        },

    },
    mounted() {
        // this.links = this.loadAll();
    },

};
</script>

<style scoped>
@font-face {
    font-family: "Segoe UI";
    src: local("SegoeUI"), local('Segoe UI')
        /* url(./../../../src/assets/fonts/SegoeUIItalic.ttf) format("woff"),
    url(./../../../src/assets/fonts/SegoeUIItalic.ttf) format("woff2"), */
        url(./../../../src/assets/fonts/SegoeUI.ttf) format("truetype");

}

.style123 {
    color: red !important;
}

.el-form-item {
    margin-bottom: 5px;
}

.form-control {
    width: 100%;
    height: 30px;
    padding: 0;
}

.el-radio {
    margin: 5px;
}

.dialog-title {
    font-size: 24px;
    font-weight: 700;
    margin-bottom: 18px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.style-tolltip {
    color: red;
    background: #ff6400;
}

.router-link-active {
    color: #ffffff
}

.router-link-active:hover {
    -webkit-transform: scale(1.1);
    text-decoration: none;
}

.dialog-footer {
    text-align: right;
    margin-top: 20px;
}

.el-form-item__label {
    font-weight: bold;
    font-size: 16px;
}

.dialog-footer button {
    padding: 5px 10px;
    font-size: 16px;
    cursor: pointer;
}

.text-align-left {
    text-align: left;
}

.btn-color {
    background-color: #ff6400;
    color: #ffffff;
}

ul {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    border-radius: 10px;
    padding: 10px;

}

ul li {
    position: relative;
    list-style: none;
    text-align: center;
    margin: 0px;
    height: 75px;
    text-align: center;
}

ul li label {
    position: relative;
    cursor: pointer;
}

ul li input[type="checkbox"] {
    position: absolute;
    opacity: 0;
}

ul li :checked~.icon-box {
    box-shadow: inset -2px -2px 5px #ff6500,
        inset 3px 3px 3px 5px rgba(0, 0, 0, 0.1);
}

ul li .fa {
    transform: scale(0.95);
}

ul li .icon-box {
    width: 40px;
    height: 40px;
    background: #ebf5fc;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: -2px -2px 5px rgba(255, 255, 255, 1),
        3px 3px 5px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
}

ul li .fa {
    font-size: 30px;
    color: #6a9bd8;
}

.style-name-check {
    font-size: 12px;
    width: 50px;
    line-height: 16px;
}

/* --------Social Icons-------- */

/* Color Variables */

/* Social Icon Mixin */

.social-icons {
    display: flex;
    position: absolute;
    bottom: 25px;
    right: 25px;
}

.social-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 40px;
    height: 40px;
    margin: 0 0.7rem;
    border-radius: 50%;
    cursor: pointer;
    font-family: "Helvetica Neue", "Helvetica", "Arial", sans-serif;
    font-size: 1.5rem;
    text-decoration: none;
    transition: all 0.15s ease;
}

.social-icon:hover {
    color: #fff;
}

.social-icon .tooltip {
    visibility: visible;
    opacity: 1;
    transform: translate(-50%, -150%);
}

.tooltip :active {
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.5) inset;
}

i {
    position: relative;
    top: 1px;
}

/* Tooltips */
.tooltip {
    display: block;
    position: absolute;
    top: 0;
    left: 50%;
    padding: 0.4rem 0.6rem;
    border-radius: 40px;
    font-size: 0.4rem;
    font-weight: bold;
    opacity: 0;
    pointer-events: none;
    text-transform: uppercase;
    transform: translate(-50%, -100%);
    transition: all 0.3s ease;
    z-index: 1;
}

.tooltip:after {
    display: block;
    position: absolute;
    bottom: 1px;
    left: 50%;
    width: 0;
    height: 0;
    content: "";
    border: solid;
    border-width: 10px 10px 0 10px;
    border-color: transparent;
    transform: translate(-50%, 100%);
}

@media only screen and (max-width: 480px) and (min-width: 370px) {
    .el-radio {
        margin: 3px;
    }

    .el-radio--medium.is-bordered {
        padding: 9px 10px 5px 5px;
        border-radius: 4px;
        height: 36px;
    }

    .el-form-item {
        margin-bottom: 0px;
    }

    ul li .icon-box {
        width: 35px;
        height: 35px;
    }

    .style-name-check {
        font-size: 10px;
        width: 50px;
        line-height: 14px;
    }
}
</style>



