    },
    methods: {
        async getDataStart(getsn) {
            axios.defaults.headers = {
                'deviceid': 'device_for_web',
                'authorization': ''
            }
            let {
                data
            } = await axios.get(`https://api.go2joy.vn/hotelapi/user/view/findUserReviewList2?offset=0&limit=50&hotelSn=${getsn}`)
            console.log('data load layout ', data)
            this.dataHotel1 = data
            if (this.dataHotel1.numOfStarReview != '') {
                this.dataStart1 = this.dataHotel1[0].numOfStarReview[0]
                this.dataStart2 = this.dataHotel1[0].numOfStarReview[1]
                this.dataStart3 = this.dataHotel1[0].numOfStarReview[2]
            } else {
                console.log('ko co data')
            }

        },
        getStartAverageMark(averageMark) {
            if (averageMark === 5) {
                return this.averageMark = 'Xut sc'
            } else if (averageMark === 4) {
                return averageMark = 'Tt'
            } else if (averageMark === 3) {
                return averageMark = 'Bnh thng'
            } else if (averageMark === 2) {
                return averageMark = 'T'
            } else if (averageMark === 1) {
                return averageMark = 'Rt t'
            } else if (averageMark === 0) {
                return averageMark = ''
            }
        },
        onChangePage(pageOfItems) {
            console.log('2132', pageOfItems)
            // update page of items
            this.pageOfItems = pageOfItems;
        },
        scrollToTop() {
            $('html, body').animate({
                scrollTop: 0
            }, 1500);
        },
    },

    mounted() {
        // let loading = this.$loading({
        //     fullscreen: true
        // })
        // setTimeout(() => {
        //         loading.close()
        //     }, 1000),

        this.$store.dispatch("GET_DATA_LIST_HOTELDETAIL", this.sn)
        this.$store.dispatch("GET_DATA_START_DETAIL", this.sn)
        this.$store.dispatch("GET_DATA_LIST_COMMENT", this.sn)

    },

    updated() {
        this.data = this.$store.state.dataHotelDetail
        // console.log('ddata layout comment', this.data)
        this.listComment = this.data.userReviewFormList
        // console.log('list comment layout comment', this.listComment)
