        // this.dataStart1 = this.$store.state.dataStart1

        // this.dataStart2 = this.$store.state.dataStart2
        // this.dataStart3 =  this.$store.state.dataStart3
         function reloadPage() {
            var currentDocumentTimestamp = new Date(performance.timing.domLoading).getTime();
            // Current Time //
            var now = Date.now();
            // Total Process Lenght as Minutes //
            var tenSec = 10 * 1000;
            // End Time of Process //
            var plusTenSec = currentDocumentTimestamp + tenSec;
            if (now > plusTenSec) {
                location.reload();
            }
        }
        reloadPage();
    }

};
