<template>
<div class="_1aU_RW ">
    <div class="_1VZIbu1 " style="padding-top:5rem !important" >
        <div class="gallery">
            <div v-for="(dataHotel, index) in  this.$store.state.dataSlide" :key="index" style="float:left">
                <div class="padding-top-bottom">
                    <!-- <router-link tag="a" :to="{ name: 'PromotionDetail', params: { sn: dataHotel.sn , action: dataHotel.action }}" replace> -->

                    <el-image @mousedown="mouseDown" @mouseup="mouseUp" @click="toDetail({ name: 'PromotionDetail', params: { sn: dataHotel.targetSn , action: dataHotel.action }}, $event)" class="border-radius res-ponsive" :src="'https://go2joy.s3-ap-southeast-1.amazonaws.com/'+ dataHotel.imagePath">
                    </el-image>
                    <!-- </router-link> -->
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import Slick from 'vue-slick';
import axios from 'axios';
export default {
    name: 'Slider',
    components: {
        Slick
    },
    data() {
        return {
            data: {},
            slickOptions: {
                slidesToShow: 1,
            },
            holdTime: 0,
            startHold: 0,

        }
    },
    updated() {
        $('.gallery').not('.slick-initialized').slick({
            autoplay: true,
            dots: false,
            arrows: false,
            autoplaySpeed: 3000,
            loop: true,
            slidesToShow: 1,
            slidesToScroll: 1,
            infinite: true,
            fade: true,
            cssEase: 'linear'
        });
    },
    methods: {
        toDetail(obj, event) {
            console.log(event, this.holdTime);
            (this.holdTime < 150) && this.$router.push(obj)
        },
        mouseDown() {
            this.startHold = Date.now();
            this.holdTime = 0;
        },
        mouseUp() {
            this.holdTime = Date.now() - this.startHold;
            this.startHold = Date.now();
            // this.startHold = 0;
        },
    },
}
</script>

<style scoped>
.slick-arrow {
    display: none !important;
}

.slick-dots {
    display: none !important;
}

#carousel-1 {
    border-radius: 30px !important;
}

#carousel-1 .carousel-item {
    border-radius: 30px !important;
}

.padding-top-bottom {
    padding: 20px 10px
}

.img-fluid {
    border-radius: 30px !important;
}

.padding-top-2rem {
    padding-top: 2rem;
}

@media only screen and (max-width: 374px) and (min-width: 320px) {
    ._1VZIbu1 {
        padding: 7rem 0 2rem 0;
    }

    .padding-top-bottom {
        padding: 20px 10px
    }
}

@media only screen and (max-width: 480px) and (min-width: 375px) {
    .padding-top-2rem {
        padding-top: 4rem;
    }

    .padding-top-bottom {
        padding: 20px 10px
    }

    ._1VZIbu1 {
        padding: 7rem 0 2rem 0;
    }
}

@media only screen and (max-width: 3840px) and (min-width: 1920px) {
    .padding-top-2rem {
        padding-top: 4rem;
    }
}

.style-slider {
    display: block;
    overflow: hidden;
    border-radius: 20px;
    margin: 0 auto 0px auto;
}

.border-radius {
    border-radius: 20px;

}

.res-ponsive {
    width: 100%;

}

.style-slider img {
    width: 100%;

}

.padd-l-r-10 {
    padding: 0 10px 0 10px;
}

.el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
}

.padd-top {
    padding-top: 30px
}

@import 'swiper/css/swiper.css';
@import './base.scss';

.swiper .swiper-pagination-bullet-custom {
    font-size: 20px;
    width: 100px !important;
    height: 100px !important;
    line-height: 100px !important;
    text-align: center;
    color: red;
    opacity: 0.7;
    background: rgba(red, 0.2);
    /* transition: all $transition-time-normal; */
}

.swiper .swiper-pagination-bullet-custom:hover {
    opacity: 1;
}

.swiper .swiper-pagination-bullet-custom .swiper-pagination-bullet-active {
    opacity: 1;
    color: #ffffff;
    background: #007aff;

}

.border-radius {
    border-radius: 20px;
}
</style>



