<script>
import VueCarousel from 'vue-carousel';
import Header from '@/components/ComponentsPartial/Header'
import AlertDialogDownApp from '@/components/DetailHotel/AlertDialogDownApp'
import Footer from '@/components/ComponentsPartial/Footer'
import {
    Loading
} from 'element-ui';
export default {
    name: 'DetailHotelListRoom',
    components: {
        Header,
        AlertDialogDownApp,
        Footer,
        VueCarousel
    },
    props: ["snroom", "snhotel"],
    data() {
        return {
            renderComponent: true,
            fullscreenLoading: false,
            loading: true,
            show: false,
            name: '',
            image: '',
            dataHotel: [],
            list: {},
            hotel1: {},

            description: '',
            sn: 0,

        }
    },

    created() {
        this.mapMaxDiscount = this.$store.state.dataHome.mapMaxDiscount
    },
    beforeMount() {
        this.$store.dispatch("GET_DATA_LIST_DETAIL_ROOM", {
            snhotel: this.snhotel,
            snroom: this.snroom,

        })
        this.$store.dispatch("GET_DATA_DETAIL_ROOM", this.snhotel)
        this.dataHotel = this.dataHotelDetail(this.$route.params.snroom, this.$route.params.mode)
        console.log('this.dataRoomHotel', this.dataHotel)

    },

    methods: {
        dataHotelDetail(snroom, mode) {
            if (mode == 2) {
                let list = [...this.$store.state.dataRoom1];

                this.list = list
                // snroom = localStorage.sn
                // console.log('snroom localStorage', snroom)
                console.log('data list', this.list)
                // this.snroom = localStorage.sn
                return this.list.find((x) => {
                    return (x.sn == snroom)
                });
                this.dataHotel = this.list
            } else {
                let list = [...this.$store.state.dataRoom2]

                this.list = list
                // snroom = localStorage.sn
                // console.log('snroom localStorage', snroom)
                console.log('data list', this.list)
                // this.snroom = localStorage.sn
                return this.list.find((x) => {
                    return (x.sn == snroom)
                });
                this.dataHotel = this.list
            }

        },
        // showView(v){
        //     if(localStorage.locale == 'vi'){

        //     }
        // },
        fullscreen() {
            this.fullscreenLoading = true;
            setTimeout(() => {
                this.fullscreenLoading = false;
                window.location.reload(true);
            }, 1000);
        },
        
    },

    updated() {
        function reloadPage() {
            var currentDocumentTimestamp = new Date(performance.timing.domLoading).getTime();
            // Current Time //
            var now = Date.now();
            // Total Process Lenght as Minutes //
            var tenSec = 10 * 1000;
            // End Time of Process //
            var plusTenSec = currentDocumentTimestamp + tenSec;
            if (now > plusTenSec) {
                location.reload();
            }
        }
        reloadPage();

    }

};
</script>

