                <div class="LbClcH">
                    <h2 class="_3vEFqF style-bold padding-choose-address" >{{ $t('Home.chooseaddress') }}</h2>
                </div>
                <div class="_2kemxe" style="padding-right:0"><button @click="show = !show" target="" class="style-hcm" as="link"><img class="style-img-local" src="./../../../src/assets/maps-and-flags copy.png" alt=""> H Ch Minh <img class="style-img-local" src="./../../../src/assets/Triangle.png" alt=""></button></div>
            </header>
            
