                        <router-link tag="a" props :to="{ name: 'CategoryAddress',  params: { sn: 7 }}">
                            <img class="img-lazy style-res" src="./../../assets/location.png">
                        </router-link>
                    </div>
                    <p class="_3vEFqF style-text-align ">{{ $t('Home.riverside') }}</p>
