                        <router-link :to="{ name: 'CategoryAddress',  params: { sn: dataHotel.sn }}">
                            <img class="img-lazy style-res" :src="'https://go2joy.s3-ap-southeast-1.amazonaws.com/'+dataHotel.imagePath">
                        </router-link>
                    </div>
                    <p class="_3vEFqF style-text-align " post-title="cutString(dataHotel.title)">{{ cutString(dataHotel.title) }}</p>
                </div>
            </a>
