<script>
import axios from 'axios'
import AlertDialog from "./AlertDialog.vue";
export default {
    name: 'Address',
    components: {
        AlertDialog
    },
    props: ["sn", "title"],
    data() {
        return {
            show: false,
            dataListAddress: {},
            titleName: '',
        };
    },
    created() {

        this.getListAddress();

    },
    methods: {
        async getListAddress() {
            
            axios.defaults.headers = {
                'deviceid': 'device_for_web',
            }
            let {
                data
            } = await axios.get(`https://api.go2joy.vn/hotelapi/home/view/findHomePageInfo?provinceName=Ho Chi Minh&userType=1`);
            // console.log('data list address', data)
            let listData = data.circleCollectionList
            // console.log('listData', listData)
            this.dataListAddress = listData
            // console.log('this.dataListAddress', this.dataListAddress)
        },

        cutString(title){
            if(localStorage.locale == 'vi')
            {
                return title.split("|")[0];
            }else{
                return title.split("|")[1]
            }
            
        }
    }
}
</script>

